.Home {
  text-align: center;
  width: 100%;
  min-height: 100vh;
  height: 100vh;
}

.Home .center-circle-container {
  width: 100%;
  height: 100px;
  position: absolute;
  top: 50%;
}

.Home .center-circle-container .center-circle {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  background: rgba(255,255,255,0.5);
  width: 15vmin;
  height: 15vmin;
  transform: scale(3.5);
  border-radius: 50%;
  border: #fff solid 2px;
}
@media only screen and (max-width: 2048px) {
  .Home {
    /* background-size: auto; */
  }
}
