.About {
  width: 100%;
  height: 100%;
}

.About .container {
  width: 85%;
  background-color: #fcfcfc;
  margin: calc(18vh + 2.5%) auto 0 auto;
  height: auto;
  max-width: 1651.4px;
  padding: 1.45vh;
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
}

.About .container .description {
  font-family: "Avant Garde Lt";
  /* transform: translateY(40%); */
  padding-top: 17%;
  margin: 2.8em 4em 4em 4em;
  height: 100%;
  width: 55%;
  max-width: 55%;
}

.About .container .description .header {
  font-size: 4vmin;
  color: rgb(118,177,212);
  display: block;
  border-bottom: 3px solid rgb(118,177,212);
  padding-bottom: 0.5em;
  margin-bottom: 0.5em;
}

.About .container .description .body {
  font-size: 2em;
  font-family: "Avant Garde Lt Medium";
  color: grey;
  line-height: 1.4em;
}

.About .container .images {
  max-width: 50%;
  height: 100%;
  width: 45%;
}


.About img {
  width: 85%;
  display: block;
  margin: 2em 1em;
}

::-webkit-scrollbar {
    /* position: absolute;
    z-index: 1; */
}

@media only screen and (max-width: 760px) {
  .About .container .images {
    transform: scale(0);
  }
  .About .container .description {
    max-width: 100%;
    width: 80%;
    padding-top: 0;
  }

  .About .container {
    display: block;
  }
}
